* {
  padding: 0;
  margin: 0;
}
*::selection {
  background: #ff8000;
  color: #222222;
}
::-webkit-scrollbar {
  width: 15px;
}
::-webkit-scrollbar-track {
  background-color: #222222;
}
::-webkit-scrollbar-thumb {
  background-color: #ff8000;
  border: 3px solid #222222;
  border-radius: 9px;
}
*::-moz-scrollbar {
  width: 15px;
}
*::-moz-scrollbar-track {
  background-color: #222222;
}
*::-moz-scrollbar-thumb {
  background-color: #ff8000;
  border: 3px solid #222222;
  border-radius: 9px;
}
img {
  max-width: 100%;
}
body {
  font-family: "Montserrat", "Helvetica", "Arial", sans-serif;
}
ul {
  list-style-type: none;
}
.App {
  min-height: 100vh;
  position: relative;
  text-align: center;
  background-color: #2c2e43;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 1;
}

a {
  text-decoration: none;
  color: #ffffff;
}
.wrapper {
  width: 90%;
  max-width: 1550px;
  margin: 0 auto;
}

.App-link {
  color: #09d3ac;
}
.section-title {
  color: white;
  font-size: 34px;
  margin-bottom: 20px;
  letter-spacing: 2px;
}
